.Container {
  width: 100vw;
  height: 167vw; /* height:width ratio = 9/16 = .5625  */
  background: lightblue;
  max-height: 100vh;
  max-width: 60vh; /* 16/9 = 1.778 */
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0; /* vertical center */
  left: 0;
  right: 0; /* horizontal center */
}
