.Container {
  position: absolute;

  width: 100%;
  height: 100%;
  background: grey;
  border-radius: 1vh;
}

.Link {
  font-size: 2vh;
  padding-left: 2vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  display: block;
  line-height: 3vh;
  text-decoration: none;
  color: black;
  font-weight: 400;
}

.Link:hover {
  transition-duration: 3s;
  background: lightgrey;
}

.Selection {
  font-weight: 900;
  padding: 3vh;
  text-align: center;
  font-size: calc(2vh);
}
