@font-face {
  font-family: myFirstFont;
  src: url(../../../assets/fonts/joy.ttf);
}

.Ani {
  animation: movet 11s;
  position: relative;
  font-size: 30px;
  margin-top: 10px;
  margin-left: 80px;
  color: red;
  font-family: 'myFirstFont';
}

@keyframes movet {
  0% {
    left: 0px;
    top: 400px;
  }

  100% {
    left: 0px;
    top: 0px;
  }
}

.Canvas {
  position: absolute;
  z-index: 0;
  border-radius: 10px;
  width: 100%;
  height: 100%;
}
